@import "./../base/_base.scss";

.allianceComponent {
  margin-top: 242px;
  @include tablet {
    margin-top: 412px;
  }
  section {
    width: $container;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    @include tablet {
      width: $container-tablet;
      flex-direction: column-reverse;
    }
    aside {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      hr {
        width: 80%;
        border: 1px solid #ededed;
      }
      .title {
        text-align: center;
        padding-bottom: 68px;
        @include tablet {
            padding-top: 40px;
        }

      }
      .alliances {
        margin: 32px 0;
        img {
          margin-right: 32px;
          &:last-child{
            @include tablet {
                margin-right: 0;
              }
          }
          @include tablet {
            margin-right: 24px;
          }
        }
      }
    }
  }
}
