@import "./../base/_base.scss";

.serviceComponent {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-top: 318px;
  @include tablet {
      padding-top: 48px;
  }
  .container {
    width: $container;
    display: flex;
    flex-direction: row;
    margin: 0 auto;
    flex: 1;
    @include tablet {
      width: $container-tablet;
      flex-direction: column;
      align-items: center;
    }
    img {
      width: 574px;
    }
    section {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      .title {
        text-align: center;
        h3 {
          font-style: italic;
          font-weight: normal;
          line-height: 36px;
        }
      }
      aside {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        width: 100%;
        justify-content: center;
        padding-top: 28px;
        article {
          width: 294px;
          // height: 182px;
          display: flex;
          align-items: flex-end;
          margin: 20px;
          img {
            margin-right: 16px;
            width: 84px;
            height: 106px;
          }
        }
      }
    }
  }
}
